/**
 *    
 * @param {number} delay :  ms
 * @returns promise
 */
export const sleep = (delay: number) =>
    new Promise<Boolean>((resolve) => {
        setTimeout(() => {
            resolve(true);
        }, delay);
    });

/**
 * 1     
 * @param length
 * @returns Array [0, 1, 2, 3, ...]
 */
export const indexArray = (length: number) => {
    const arr = Array.from({length}, (v, i) => i);
    return arr;
};
