import {useState} from 'react';

import {cancelApiRequest, postPromptMessage} from '@/api/prompt';
import {parsePromptImages, TImage} from '@/model/prompt';

function usePrompt() {
    const [value, setValue] = useState<string>('');
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [imageList, setImageList] = useState<TImage[] | null>(null);

    //    
    const sendPromptMessage = () => {
        if (!value) {
            alert(' .');
            return;
        }

        setIsLoading(true);
        postPromptMessage({prompt: value})
            .then(({generatedImage}) => {
                setImageList(parsePromptImages(generatedImage.images));
            })
            .catch((err) => {
                console.log(`[cancelApiRequest]`, err);
            })
            .finally(() => {
                setIsLoading(false);
            });
    };

    //  
    const restartDraw = () => {
        if (isLoading) cancelApiRequest('restartDraw');
        setValue('');
        setImageList(null);
        setIsLoading(false); // [To-do] api   loaidng=false 
    };

    return {
        value,
        setValue,
        isLoading,
        imageList,
        sendPromptMessage,
        restartDraw,
    };
}

export default usePrompt;
